* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 100%;
  background-size: cover;
}

body {
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
}

.head, .links {
  text-align: center;
}

.head, .content {
  width: 90%;
  margin: 0 auto;
}

img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  padding: 1px;
  background-color: #666;
  transition: all 0.5s ease-in-out;
  -webkit-filter: grayscale(50%);
          filter: grayscale(50%);
  margin-top: -10px;
}

.profile-image {
  
}

.profile-card {
  width: 100%;
  background-color: hsla(0, 0%, 100%, .5);
  border-radius: 4px;
  box-shadow: hsla(0, 0%, 0%, .8) 10px 10px 80px;
  background-image: url(/static/media/caspar-camille-rubin-669071-unsplash.608f0788.jpg);
  color: yellow;
}

.head, .about {
  margin-bottom: 10%;
}

.content {
  padding: 5% 10%;
}


img:hover {
  -webkit-transform: scale(2) translate(0, 100px);
          transform: scale(2) translate(0, 100px);
  transition: 2s;
  -webkit-filter: grayscale(20%);
          filter: grayscale(20%);
}

@media (max-width: 600px){
  img{
    width: 80px;
    height: 80px;
  }

  img:hover {
  -webkit-transform: scale(1);
          transform: scale(1);
}
}

